.App {
  text-align: center;
}

/* ========= HEADER ========= */

.App-header {
  background-color: #222;
  height: 40px;
  padding: 1px 0 20px 0;
  color: #fff;
}

.App-title {
  font-size: 1.5em;
}

.menu-button {
  position: absolute;
  left: 0;
  background: none;
  border: none;
  margin: 10px 5px;
}

.menu-container {
    display: inline-block;
    cursor: pointer;
}

.menu-bar1, .menu-bar2, .menu-bar3 {
  width: 35px;
  height: 5px;
  background-color: #cecece;
  margin: 6px 0;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

/* Rotate first bar */
.change .menu-bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
  -ms-transform: rotate(-45deg) translate(-9px, 6px) ;
      transform: rotate(-45deg) translate(-9px, 6px) ;
}

/* Fade out the second bar */
.change .menu-bar2 {
    opacity: 0;
}

/* Rotate last bar */
.change .menu-bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
  -ms-transform: rotate(45deg) translate(-8px, -8px) ;
      transform: rotate(45deg) translate(-8px, -8px) ;
}

/* ========= PLACESLIST ========= */

.list-container {
  display: block;
  position: absolute;
  background-color: #222;
  height: calc(100vh - 89px);
  width: 270px;
  margin-bottom: 0;
  -webkit-transition: all .9s;
  -o-transition: all .9s;
  transition: all .9s;
  z-index: 100;
  overflow: auto;
}

.list-container ul li button {
  background: none;
  border: none;
  color: #fff;
  text: none;
}

.close {
  display: none;
}

input[type=text] {
    width: 150px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 3px;
    font-size: 16px;
    background-color: white;
    background-position: 10px 10px;
    background-repeat: no-repeat;
    padding: 5px 20px 5px 10px;
    margin: 10px 0;
    -webkit-transition: width 0.8s;
    -o-transition: width 0.8s;
    transition: width 0.8s;
}

input[type=text]:focus {
    width: 100%;
}

.list-container ul {
  color: #fff;
  list-style: none;
  padding: 0 0 15px;
}

.place-item {
  padding: 15px 0 15px 0;
}

.place-item:hover {
  background-color: #555;
}

/* ========= MAP ========= */

.map-container {
  height: calc(100vh - 89px);
  width: 100%;
}

.map {
  height: 100%;
}

/* ========= MAP ========= */

footer {
  height: 28px;
  background-color: #222;
  color: #fff
}

footer span {
  vertical-align: -webkit-baseline-middle;
  width: auto;
}

footer a:link, footer a:visited {
  color: #fff
}

@media screen and (max-width: 375px) {
  .App-title {
    width: calc(100% - 35px);
    margin-left: 35px;
    font-size: 1.4em;
  }
  .menu-button {
    margin-top: 9px;
  }
  .menu-bar1, .menu-bar2, .menu-bar3 {
    width: 30px;
  }
  .list-container {
    height: calc(100vh - 85px);
  }
  footer span{
    font-size: 0.7em;
  }
}

@media screen and (max-width: 325px) {
  .App-header {
    height: 30px;
  }
  .App-title {
    font-size: 1.2em;
  }
  .menu-button {
    margin-top: 5px;
  }
}



